/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import {
  Button, Card, CardActionArea, CardActions, CardMedia, Grid, makeStyles
} from '@material-ui/core';
import KatalonTour from '../../../variables/KatalonTour';
import StartPageLink from '../../start-page/components/StartPageLink';


const useStyles = makeStyles({
  root: {
    height: '100%',
    justifyContent: 'space-between',
    flexDirection: 'column',
    display: 'flex',
    background: '#FCFDFF',
    border: '3px solid #fff',
    borderRadius: '24px !important',
    textAlign: 'center',
    animation: 'fadeIn 1.5s ease-in-out',
    boxShadow: '0 7px 13px #ECF4FF'
  },
  content: {
    padding: '30px 20px 10px'
  },
  carActionArea: {
  },
  mediaWrapper: {
  },
  media: {
    height: 0,
    backgroundSize: 'contain',
    paddingTop: '66.337423%'
  },
  cardActions: {
    spacing: '1',
    justify: 'space-between',
    alignItems: 'center',
    flexDirection: 'column',
    flex: '1 1 auto',
    padding: '10px 25px 20px'
  },
  title: {
    marginBottom: '10px',
    whiteSpace: 'pre-wrap',
    fontWeight: 'bold',
    width: '100%'
  },
  actionButton: {
    textTransform: 'none',
    padding: '7px 18px',
    fontSize: '12px'
  },
  subButton: {
    color: '#656488',
    fontSize: '12px'
  }
});

export const QuickStartTourV2 = ({
  id, title, image, disabled, canSkip, action, onStartTour, hideSkipButton, ...props
}) => {
  const classes = useStyles();

  function handleStartTour() {
    if (onStartTour) {
      onStartTour(id);
    }
  }

  function handleSkipTour() {
    if (onStartTour) {
      onStartTour(KatalonTour.SKIP_TOUR);
    }
  }

  return (
    <Card className={classes.root} {...props}>
      <div className={classes.content}>
        <div className={classes.mediaWrapper}>
          <CardActionArea className={classes.carActionArea}>
            <CardMedia
              className={classes.media}
              image={image}
              title={title}
            />
          </CardActionArea>
        </div>
      </div>
      <CardActions className={classes.cardActions}>
        <Grid item style={{ width: '100%', flex: '1 1 auto' }}>
          <div className={classes.title}>
            {title}
          </div>
        </Grid>
        <Grid item style={{ flex: '0 1 auto' }}>
          <Button
            size="small"
            color="primary"
            variant="contained"
            disableElevation
            className={classes.actionButton}
            onClick={handleStartTour}
            disabled={disabled}
          >
            <b>{action || 'Start learning'}</b>
          </Button>
        </Grid>
        {!hideSkipButton ? (
          <Grid item>
            <StartPageLink
              className={classes.subButton}
              href=""
              style={{ visibility: canSkip && !disabled ? 'visible' : 'hidden' }}
              onClick={handleSkipTour}
            >
              Maybe later
            </StartPageLink>
          </Grid>
        ) : (
          <Grid item style={{ margin: '4%', padding: '0' }} />
        )}
      </CardActions>
    </Card>
  );
}
