
import { Grid } from '@material-ui/core';
import React from 'react';
import KSActionService from '../../../services/KSActionService';
import KSService from '../../../services/KSService';
import KatalonTours from '../../../variables/KatalonTours';
import { QuickStartTourV2 } from './QuickStartTourV2';


export const QuickStartTourRow = ({ hideSkipButton = false }) => {
  const url = new URLSearchParams(window.location.search);
  const tourType = url.get('tour_type');
  const targetTour = url.get('next_tour');
  const rawCompletedTours = url.get('completed_tours');
  const [completedTours, setCompletedTours] = React.useState(
    rawCompletedTours && JSON.parse(rawCompletedTours)
  );
  const selectedTours = KatalonTours[tourType];

  const handleStartTour = (tourId) => {
    KSActionService.startTour(tourId);
  }
  const keepTrackTourProgress = () => {
    KSService.fetchUserProfile()
      .then((profile) => {
        if (profile) {
          setCompletedTours(profile.completedTours || completedTours);
        }
        setTimeout(() => {
          keepTrackTourProgress();
        }, 3000);
      });
  }
  React.useEffect(() => {
    keepTrackTourProgress();
  }, []);// eslint-disable-line react-hooks/exhaustive-deps

  var flexboxSize = selectedTours.length > 4 ? 2 : 3;

  const getButton = (id, action) => {
    var isDisable = !!targetTour && id !== targetTour;
    if (!!targetTour) {
      if (isDisable) {
        return completedTours.includes(id) ? 'Finished' : 'Not Started';
      }
      return action;
    }

    return completedTours.includes(id) ? 'Replay' : action;
  }

  return (
    <Grid container spacing={2} wrap="wrap" justifyContent="center" alignItems="stretch">
      {selectedTours.map((tour, index) => (
        <Grid item key={tour.id} xs={12} sm={5} md={flexboxSize} lg={flexboxSize}>
          <QuickStartTourV2
            id={tour.id}
            title={index + 1 + ". " + tour.title}
            image={tour.image}
            onStartTour={handleStartTour}
            disabled={!!targetTour && tour.id !== targetTour}
            canSkip={!!targetTour}
            action={getButton(tour.id, tour.action)}
            hideSkipButton={hideSkipButton}
          />
        </Grid>
      ))}
    </Grid>
  );
}
